* {
  box-sizing: border-box;
}
html {
  width: 100%;
  height: 100%;
  background-color: #99cccc;
  padding: 0;
  margin: 0;
}

body {
  padding: 10px 16px;
  margin: 0;
  background: linear-gradient(#ffd1dc, #aec6cf);
}
.logo {
  width: 160px;
}
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 30px;
}
.panel {
  position: relative;
  width: 100%;
  min-height: 50px;
  background-color: #fff;
  margin-top: 20px;
  border-radius: 6px;
  display: inline-block;
  box-shadow: 0 0 5px 2px rgba(230, 157, 42, 0.08);
  padding: 15px 20px;
  text-align: left;
  &:after {
    content: " ";
    display: block;
    clear: both;
  }
}
.connect-panel p {
  color: #999;
}

.el-row {
  margin-bottom: 15px;
}
.command-panel .button-wrap {
  float: left;
  margin: 0 10px 10px 0;
}

.panel-title {
  width: 100%;
  height: 30px;
  text-align: left;
  font-size: 17px;
  margin-bottom: 12px;
  font-weight: bold;
  border-bottom: 1px solid rgb(209, 206, 206);
  border-bottom-style: dashed;
  padding-top: 10px;
  padding-bottom: 40px;
}
.findBluetooth {
  padding: 5px;
  font-size: 13px;
  background-color: #d7ebeb;
  font-weight: bold;
}
.findBluetooth:hover {
  background-color: #db7880;
  color: white;
}
.addedFile {
  font-size: 15px;
}

.info-group {
  position: relative;
  float: left;
  display: block;
  text-align: left;
  width: 48%;
  margin-bottom: 20px;
  margin-right: 1%;
  overflow: hidden;
  min-height: 45px;
  .label {
    font-size: 14px;
    font-weight: bold;
    color: #758699;
  }
  .value {
    width: 96%;
    font-size: 14px;
    overflow: hidden;
    text-overflow: ellipsis;
  }
}
.code {
  display: block;
  width: 100%;
  height: 100px;
  background-color: #333333;
  color: #e4edff;
  padding: 6px 10px;
}
.copyright {
  width: 100%;
  color: white;
  padding: 20px 0;
}
.copyright a {
  color: white;
}
.label-output {
  width: 80%;
  margin-left: 10%;
}
.block {
  padding: 20px;
  margin-left: 2%;
  font-weight: bold;
  font-size: 20px;
  color: #466969;
}

.arrow {
  width: 97%;
  margin-left: 1.5%;
  border-bottom: 1px solid rgb(119, 151, 192);
  border-bottom-style: dashed;
}

.slider-picker {
  width: 100%;
  max-width: 420px;
}

.pics {
  display: flex;
}
.blocks {
  background-color: #f0f5fc;
  width: 100%;
  border-radius: 5px;
  padding-bottom: 5%;
  margin-bottom: 5%;
}
.outputPic {
  width: 100%;
  margin-left: 5%;
  border: 1px solid black;
  opacity: 0.25;
  transition: 0.5s ease;
}
.outputPicSel {
  width: 100%;
  margin-left: 5%;
  border: 1px solid black;
  opacity: 1;
  transition: 0.5s ease;
}
.cameraData {
  margin: auto;
  display: inline-block;
  padding: 10px;
}
.outputType {
  width: 25%;
  padding: 5px;
}
.outputPic:hover {
  opacity: 1;
}
.servomotor {
  display: block;
  font-size: 16px;
}
.servomotor span {
  margin-right: 2%;
}
.showOutputType {
  margin-left: 2%;
  font-size: 15px;
  padding: 20px;
  width: 90%;
}
.direction {
  font-size: 16px;
  font-weight: 700;
  border: 1px solid #aaa;
  background-color: #fff;
  color: #444;
}
.direction:focus {
  outline: none;
}

@media screen and (min-width: 900px) {
  .panel-container {
    display: flex;
    justify-content: space-between;
  }
  .panel {
    margin-right: 10px;
  }
  .copyright {
    position: relative;
    bottom: 0px;
  }
}
